export interface MessageMonitorConfig {
  type: 'in-memory' | 'redis' | 'disabled';
}

export enum ConfiguredConnectorTypes {
  API_SINK = 'api-sink',
  API_SOURCE = 'api-source',
  DUMMY_SINK = 'dummy-sink',
  DUMMY_SOURCE = 'dummy-source',
  DUMMY_NODE = 'dummy-node',
  FILE_COPY = 'file-copy',
  FILE_SINK = 'file-sink',
  FILE_SOURCE = 'file-source',
  SAMBA_SINK = 'samba-sink',
  SAMBA_SOURCE = 'samba-source',
  SQL_SINK = 'sql-sink',
  SQL_SOURCE = 'sql-source',
  MQTT = 'mqtt',
  CUBE_QUERY_RUNNER = 'cube-query-runner',
  FACTORY_NEBULA_SOURCE = 'factory-nebula-source',
  BYSTRONIC = 'bystronic',
  IMAP_SOURCE = 'imap-source',
  IMAP_SINK = 'imap-sink',
  OPCUA_SOURCE = 'opcua-source',
  OPCUA_SINK = 'opcua-sink',
  AI_AGENT = 'ai-agent',
  MICROSOFT_OFFICE365_EMAIL = 'microsoft-office365-email',
}

export interface ConnectorConfig {
  type: ConfiguredConnectorTypes;
  identifier: string;
}

export interface RedisMessageMonitorConfig extends MessageMonitorConfig {
  type: 'redis';
  url: string;
  ttl?: number;
}

export interface InMemoryMessageMonitorConfig extends MessageMonitorConfig {
  type: 'in-memory';
  messagesToKeep: number;
}

export interface DisabledMessageMonitorConfig extends MessageMonitorConfig {
  type: 'disabled';
}

export interface TopicRegex {
  pattern: string;
  flags: string;
}

export interface FileRegex {
  pattern: string;
  flags: string;
}

export function isTopicRegex(obj: TopicRegex | string): obj is TopicRegex {
  if (typeof obj === 'string') {
    return false;
  }

  return (
    obj !== undefined &&
    obj !== null &&
    obj.pattern !== undefined &&
    typeof obj.pattern === 'string' &&
    obj.flags !== undefined &&
    typeof obj.flags === 'string'
  );
}

export interface CubeConfigHeaders {
  Authorization: string;
}

export interface CubeConfig {
  auth0_client_secret?: string;
  auth0_client_id?: string;
  auth0_audience?: string;
  auth0_token_url?: string;
  authorization?: string;
  apiUrl: string;
  headers?: CubeConfigHeaders;
  redis_url?: string;
}

export function isCubeConfigType(obj: CubeConfig): obj is CubeConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    obj.authorization !== undefined &&
    typeof obj.authorization === 'string' &&
    obj.apiUrl !== undefined &&
    typeof obj.apiUrl === 'string'
  );
}

export interface AwsKafkaSaslConfig {
  region?: string;
  accessKeyId?: string;
  secretAccessKey?: string;
  mechanism: 'aws';
}

export function isAwsKafkaSalsConfig(
  obj: AwsKafkaSaslConfig,
): obj is AwsKafkaSaslConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    (obj.region === undefined || typeof obj.region === 'string') &&
    (obj.accessKeyId === undefined || typeof obj.accessKeyId === 'string') &&
    (obj.secretAccessKey === undefined ||
      typeof obj.secretAccessKey === 'string') &&
    obj.mechanism === 'aws'
  );
}

export interface DatabaseConfig {
  uri: string;
  ssl?: boolean;
  tenantPassword?: string;
}

export interface ActionConfig {
  timeSensitive: boolean;
}

export interface KafkaBrokerConfig {
  groupId: string;
  clientId: string;
  brokers: Array<string>;
  consumerTopics?: Array<string | TopicRegex>;
  sasl?: AwsKafkaSaslConfig;
  intervalCheckForNewTopics?: number;
  disableLogs?: boolean;
  autoCommitThreshold?: number;
  autoCommitInterval?: number;
  partitionsConsumedConcurrently?: number;
  messageMonitor?:
    | RedisMessageMonitorConfig
    | InMemoryMessageMonitorConfig
    | DisabledMessageMonitorConfig;
  useConfluentLibrary?: boolean;
  newConsumerProtocol?: boolean;
}

export function isKafkaBrokerConfigType(
  obj: KafkaBrokerConfig,
): obj is KafkaBrokerConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    obj.groupId !== undefined &&
    typeof obj.groupId === 'string' &&
    obj.clientId !== undefined &&
    typeof obj.clientId === 'string' &&
    obj.brokers !== undefined &&
    Array.isArray(obj.brokers) &&
    (obj.sasl === undefined ? true : isAwsKafkaSalsConfig(obj.sasl))
  );
}

export function isActionConfigType(obj: any): obj is ActionConfig {
  return (
    obj?.timeSensitive !== undefined && typeof obj.timeSensitive === 'boolean'
  );
}

export function isDatabaseConfigType(
  obj: DatabaseConfig,
): obj is DatabaseConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    obj.uri !== undefined &&
    typeof obj.uri === 'string'
  );
}

export interface BaseConnectorConfig {
  processIdentifier: string;
  tenantIdentifier: string;
  datasourceIdentifier: string;
  kafka: KafkaBrokerConfig;
  action?: ActionConfig;
}

export function isBaseConnectorConfigType(
  obj: BaseConnectorConfig,
): obj is BaseConnectorConfig {
  return (
    obj.processIdentifier !== undefined &&
    typeof obj.processIdentifier === 'string' &&
    isKafkaBrokerConfigType(obj.kafka) &&
    obj.tenantIdentifier !== undefined &&
    typeof obj.tenantIdentifier === 'string' &&
    obj.datasourceIdentifier !== undefined &&
    typeof obj.datasourceIdentifier === 'string'
  );
}

export interface AiAgentConfig {
  openai: {
    apiKey: string;
    model:
      | 'gpt-3.5-turbo'
      | 'gpt-4'
      | 'gpt-4-turbo'
      | 'gpt-4o'
      | 'gpt-4o-mini'
      | 'o1-preview'
      | 'o1-mini';
    temperature?: number;
    timeout?: number;
  };
}
